<template>
  <v-container>
    <v-row class="mt-n4">
      <v-col cols="1" />
      <v-col
        cols="8"
        md="6"
        class="mt-0"
      >
        <v-card class="v-card--material pa-3">
          <div class="d-flex grow flex-wrap">
            <v-sheet
              color="primary"
              max-height="90"
              width="auto"
              elevation="6"
              class="px-4 py-2 text-start v-card--material__heading mb-n6"
            >
              <div
                class="text-h4 font-weight-light white--text"
                v-text="'任务'+local_qid"
              />
            </v-sheet>

            <v-container class="pt-6">
              <v-row
                justify="center"
                align="center"
                class="my-n4"
              >
                <v-col
                  cols="9"
                  class="text-h3 text-left mx-0"
                >
                  目标：{{ finalPoi[0] }}
                </v-col>
                <v-col
                  cols="3"
                  class="text-h4 text--disabled text-right mx-0"
                  v-text=" (finalPoi[2] < 1000) ? finalPoi[2]+'m' : (finalPoi[2]/1000).toFixed(1)+'km' "
                />
                <v-col
                  cols="12"
                  class="text-h4 text--disabled mx-0 mt-n4"
                >
                  {{ finalPoi[1] }}
                </v-col>
              </v-row>
              <v-divider class="mt-2 mb-2" />
              <v-row>
                <v-col cols="4">
                  <v-btn
                    :color="'primary'"
                    class="text-h5 py-0"
                    :disabled="local_qid <= startQid+1"
                    @click="setTask(local_qid-1)"
                    v-text="'上一个'"
                  />
                </v-col>
                <v-col cols="4">
                  <v-btn
                    :color="'primary'"
                    class="text-h5 py-0"
                    @click="replay"
                    v-text="'重新演示'"
                  />
                </v-col>
                <v-col cols="4">
                  <v-btn
                    :color="'primary'"
                    class="text-h5 py-0"
                    :disabled="local_qid >= qid"
                    @click="setTask(local_qid+1)"
                    v-text="'下一个'"
                  />
                </v-col>
              </v-row>
              <v-row>
                <v-col
                  cols="9"
                  class="text-h3 text-left mx-0"
                >
                  查询词 {{ '： ' + q_list[page-1] }}
                </v-col>
                <v-col
                  v-show="countDownTime > 0"
                  cols="3"
                  class="text-h3 text--disabled text-right"
                  v-text="'('+countDownTime+'s)'"
                />
              </v-row>
              <v-row
                class="mb-3"
                justify="start"
              >
                <v-pagination
                  v-model="page"
                  :length="pageLength"
                  :total-visible="6"
                  @input="onPageChange"
                />
              </v-row>
              <v-divider class="mt-0" />
              <v-list
                class="pt-0"
                disabled
              >
                <v-list-item-group
                  v-model="clickPos"
                  :page.sync="page"
                >
                  <template v-for="(poi, i) in poi_lists[page-1]">
                    <v-list-item :key="i">
                      <v-list-item-content>
                        <v-row
                          justify="center"
                          align="center"
                          class="my-n4"
                        >
                          <v-col
                            cols="9"
                            class="text-h3 text-left mx-0"
                          >
                            {{ poi[0] }}
                          </v-col>
                          <v-col
                            cols="3"
                            class="text-h4 text--disabled text-right mx-0"
                            v-text=" (poi[2] < 1000) ? poi[2]+'m' : (poi[2]/1000).toFixed(1)+'km' "
                          />
                          <v-col
                            cols="12"
                            class="text-h4 text--disabled mx-0 mt-n4"
                          >
                            {{ poi[1] }}
                          </v-col>
                        </v-row>
                      </v-list-item-content>
                    </v-list-item>
                    <v-divider :key="i+100" />
                  </template>
                </v-list-item-group>
              </v-list>
            </v-container>
          </div>
        </v-card>
      </v-col>
      <v-col
        v-show="page === pageLength"
        cols="3"
        md="3"
        class="mt-8"
      >
        <v-row justify="start">
          <v-col
            cols="12"
            class="text-h3 mx-0 mb-n2"
          >
            满意度
          </v-col>

          <v-col
            v-for="(name, i) in ['非常满意\u3000', '比较满意\u3000', '一般\u3000\u3000\u3000', '较不满意\u3000', '非常不满意']"
            :key="i"
            cols="12"
          >
            <v-btn
              :color="'primary'"
              class="text-h4"
              @click="question(5-i)"
            >
              {{ name }}
            </v-btn>
          </v-col>
        </v-row>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
  import { mapState } from 'vuex'
  import qs from 'qs'

  export default {
    data () {
      return {
        page: 1,
        countDownTime: -1,
        pageLength: -1,
        q_list: [],
        poi_lists: [],
        rank: -1,
        finalPoi: [],
        time_points: [],
        local_qid: -1,
        clickPos: -1,
        startTime: 0,
      }
    },

    computed: {
      ...mapState(['allQuestionData', 'userName', 'startQid', 'qid']),
    },

    mounted () {
      const data = this.$route.params
      this.$store.commit('ADD_QUESTION_DATA', data)
      this.local_qid = data.qid + 1
      this.$store.commit('SET_START_QID', data.qid)
      this.$store.commit('SET_QID', data.qid + 1)
      this.setValue(data)
    },

    methods: {
      question (grade) {
        const timeCost = (new Date()).valueOf() - this.startTime
        this.$axios.post(
          '/api/question',
          qs.stringify({ userName: this.userName, qid: this.local_qid, grade: grade, timeCost: timeCost }),
        ).then(res => {
          const data = res.data
          if (data.ended === true) {
            this.$router.push('thanks')
          } else if (data.repost === true) {
            this.setTask(this.local_qid + 1)
          } else {
            this.$store.commit('SET_QID', data.qid + 1)
            this.$store.commit('ADD_QUESTION_DATA', data)
            this.local_qid = data.qid + 1
            this.setValue(data)
          }
        })
      },
      setValue (data, replay = true) {
        this.rank = data.rank
        this.q_list = data.q_list
        this.poi_lists = data.poi_lists
        this.pageLength = data.q_list.length
        this.time_points = data.time_points
        this.finalPoi = data.poi_lists[data.poi_lists.length - 1][data.rank]
        this.startTime = (new Date()).valueOf()
        if (replay) {
          this.replay()
        } else {
          this.page = this.q_list.length
          this.clickPos = this.rank
        }
      },
      setTask (id) {
        this.local_qid = id
        const data = this.allQuestionData[id - 1]
        this.setValue(data, false)
      },
      replay () {
        this.page = 1
        this.clickPos = (this.page === this.pageLength) ? this.rank : -1
        this.countDownTime = -1
        for (let i = 0; i < this.time_points.length; i++) {
          const t = this.time_points[i]
          if (i === 0 && t > 5) {
            this.countDown(t)
          }
          setTimeout(() => {
            this.page += 1
            this.clickPos = (this.page === this.pageLength) ? this.rank : -1
            if (i + 1 < this.time_points.length) {
              const t1 = this.time_points[i + 1]
              if (t1 - t > 5) {
                this.countDown(t1 - t)
              }
            } else {
              this.countDownTime = -1
            }
          }, t * 1000)
        }
      },
      countDown (t) {
        this.countDownTime = t
        if (t === 0) {
          return
        }
        setTimeout(() => {
          this.countDown(t - 1)
        }, 1000)
      },
      onPageChange (page) {
        this.page = page
        this.clickPos = (page === this.pageLength) ? this.rank : -1
      },
    },
  }
</script>
